/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-wcrh-custom-spacer {
	padding-top: 1rem;
	padding-bottom: 1rem;
}

.wp-block-wcrh-custom-spacer.spacer-xhalf {
	padding-top: 0.5rem;
	padding-bottom: 0.5rem;
}

.wp-block-wcrh-custom-spacer.spacer-x2 {
	padding-top: 2rem;
	padding-bottom: 2rem;
}

.wp-block-wcrh-custom-spacer.spacer-x4 {
	padding-top: 4rem;
	padding-bottom: 4rem;
}

@media (max-width: 640px) {
	.wp-block-wcrh-custom-spacer {
		padding-top: 0.5rem;
		padding-bottom: 0.5rem;
	}

	.wp-block-wcrh-custom-spacer.spacer-xhalf {
		padding-top: 0.25rem;
		padding-bottom: 0.25rem;
	}

	.wp-block-wcrh-custom-spacer.spacer-x2 {
		padding-top: 1rem;
		padding-bottom: 1rem;
	}

	.wp-block-wcrh-custom-spacer.spacer-x4 {
		padding-top: 3rem;
		padding-bottom: 3rem;
	}
}
